import { Injectable, Inject } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, from, throwError } from 'rxjs';
import { catchError, switchMap } from 'rxjs/operators';

import { AvlAuthService } from './avl-auth.service';
import { INTERCEPTOR_BASE_PATHS } from '../variables';

@Injectable()
export class AvlTokenInterceptor implements HttpInterceptor {
  protected enabled = true;

  constructor(
    public auth: AvlAuthService,
    @Inject(INTERCEPTOR_BASE_PATHS) protected basePaths: string[]
  ) {
    for (const i in this.basePaths) {
      if (this.basePaths.hasOwnProperty(i)) {
        this.basePaths[i] = this.basePaths[i].toLowerCase();
      }
    }
  }

  private setAuthHeader(req: HttpRequest<any>): HttpRequest<any> {
    const access_token = this.auth.currentUser.access_token;
    const header = `Bearer ${access_token}`;
    const headers = req.headers.set('Authorization', header);

    return req.clone({ headers });
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (!this.enabled) {
      return next.handle(req);
    }

    // check if we need to add token into request
    const url = req.url.toLowerCase();
    for (const basePath of this.basePaths) {
      if (url.startsWith(basePath)) {
        if (this.auth.currentUser != null) {
          req = this.setAuthHeader(req);
        }
        break;
      }
    }

    return next.handle(req).pipe(
      catchError((err: HttpErrorResponse) => {
        if (err.status === 401) {
          return from(this.auth.refreshTokens()).pipe(
            switchMap(_ => next.handle(this.setAuthHeader(req))),
            catchError(_ => throwError(err))
          );
        }
        return throwError(err);
      })
    );
  }
}
